import { ClientStorageData } from './ClientStorageData';
import { ClientStore } from './ClientStore';

export function createStorage<T>(base: Storage): ClientStore<T> {
  return {
    getItem(key: string) {
      const value = base.getItem(key);
      return value == null ? null : (JSON.parse(value) as ClientStorageData<T>);
    },

    setItem(key: string, value: ClientStorageData<T>) {
      if (value === null) {
        base.removeItem(key);
      } else {
        base.setItem(key, JSON.stringify(value));
      }
    },

    removeItem(key: string) {
      base.removeItem(key);
    },
  };
}
