import { ClientStorage } from './ClientStorage';
import { IClientStorage } from './IClientStorage';
import { TransformOptions } from './TransformOptions';

const identity = <T>(x: T) => x;

export class ClientStorageTransformer<Result, Origin>
  implements IClientStorage<Result> {
  // , ClientStorage<Result> {
  private readonly def: Result;
  private _cache: Result;
  private parentCache: Origin;

  private readonly revert: (x: Result) => Origin | Promise<Origin>;
  private readonly apply:
    | ((x: Origin) => Promise<Result>)
    | ((x: Origin) => Result);

  // uncomment to see why I did this
  // // this is just to make TS happy
  // // https://github.com/Microsoft/TypeScript/issues/18499
  // private default!: Result;
  // private store!: ClientStore<Result>;
  // private loadPromise!: Promise<null> | null;
  // private _isCached!: boolean;
  // private _lastUpdated!: Date;
  // private transform!: <Result>(options: TransformOptions<Result, Result>) => ClientStorageTransformer<Result, Result>;
  // private setValue!: Function;
  // private save!: Function;
  // private loadUnsafe!: Function;

  get key() {
    return this.parent.key;
  }

  get version() {
    return this.parent.version;
  }

  get isLoading() {
    return this.parent.isLoading;
  }

  get lastUpdated() {
    return this.parent.lastUpdated;
  }

  get cache() {
    if (this.parentCache !== this.parent.cache) {
      this.setCache(this.parent.cache);
    } else if (this._cache === this.def) {
      this.get();
    }

    return this._cache;
  }

  constructor(
    private readonly parent: ClientStorage<Origin>,
    options: TransformOptions<Result, Origin>,
  ) {
    this.def =
      'default' in options
        ? options.default
        : (options.transform || identity)(parent.cache);

    this._cache = this.def;
    this.parentCache = parent.cache;
    this.apply = options.transform || identity;
    this.revert = options.revert || identity;
  }

  get() {
    return this.parent.get().then(x => this.setCache(x));
  }

  set(value: Result) {
    return Promise.resolve(this.revert(value)).then(x => this.parent.set(x));
  }

  load() {
    return this.parent.load().then(x => this.setCache(x));
  }

  reset() {
    const result = this.parent.reset();
    this._cache = this.def;
    return result.then(this.apply);
  }

  private setCache(value: Origin) {
    this.parentCache = this.parent.cache;
    return Promise.resolve(this.apply(value)).then(x => (this._cache = x));
  }
}
