import { ClientStorageData } from './ClientStorageData';
import { ClientStorageOptions } from './ClientStorageOptions';
import { ClientStorageTransformer } from './ClientStorageTransformer';
import { ClientStore } from './ClientStore';
import { createStorage } from './createStorage';
import { ensureKeyIsUnique } from './ensureKeyIsUnique';
import { IClientStorage } from './IClientStorage';
import { TransformOptions } from './TransformOptions';

const DEFAULT_VERSION = 0;
const toPromise = <T>(x: T | Promise<T>) => Promise.resolve(x);

export class ClientStorage<Value = unknown> implements IClientStorage<Value> {
  private readonly default: Value;
  private readonly store: ClientStore<Value>;

  readonly version: number;

  private loadPromise: Promise<null> | null = null;
  private _cache: Value;
  private _isCached = false;
  private _lastUpdated = new Date(0);

  get cache() {
    if (!this._isCached && !this.loadPromise) {
      this.load();
    }

    return this._cache;
  }

  get isLoading() {
    return this.loadPromise != null;
  }

  get lastUpdated() {
    return this._lastUpdated;
  }

  constructor(
    readonly key: string,
    {
      store = 'localStorage',
      version = DEFAULT_VERSION,
      default: def,
      skipUniqueKeyValidation,
    }: ClientStorageOptions<Value>,
  ) {
    if (!skipUniqueKeyValidation) {
      ensureKeyIsUnique(key);
    }

    if (store === 'localStorage') {
      store = createStorage(localStorage);
    } else if (store === 'sessionStorage') {
      store = createStorage(sessionStorage);
    }

    this.store = store;

    this.version = version;
    this.default = def as Value;
    this._cache = def as Value;
  }

  get() {
    if (this._isCached) {
      return toPromise(this._cache);
    }

    return this.load().then(() => this._cache);
  }

  set(value: Value) {
    this.setValue(value);
    return toPromise(this.save(value));
  }

  transform<Result>(options: TransformOptions<Result, Value>) {
    return new ClientStorageTransformer<Result, Value>(this, options);
  }

  load() {
    if (!this.loadPromise) {
      this.loadPromise = this.loadUnsafe().then(
        () => (this.loadPromise = null),
      );
    }

    return this.loadPromise.then(() => this._cache);
  }

  reset() {
    const value = this._cache;
    this._isCached = false;
    this._cache = this.default;
    return toPromise(this.store.removeItem(this.key)).then(() => value);
  }

  private save(value: Value) {
    const data: ClientStorageData<Value> = {
      data: value,
      version: this.version,
      updated: Date.now(),
    };

    return this.store.setItem(this.key, data);
  }

  private loadUnsafe() {
    const response = this.store.getItem(this.key);

    if (isPromise(response)) {
      return response.then(x => this.onStoreResponse(x));
    }

    // if response is synchronous we want to set `this._cache` immediately
    this.onStoreResponse(response);
    return Promise.resolve();
  }

  private onStoreResponse(response: ClientStorageData<Value> | null) {
    if (response === null) {
      this._cache = this.default;
      return;
    }

    const { data, updated, version } = response;

    if (version !== this.version) {
      this.reset();
      return;
    }

    return this.setValue(data, updated);
  }

  private setValue(value: Value, updated = Date.now()) {
    this._lastUpdated = new Date(updated);
    this._isCached = true;
    this._cache = value;
  }
}

function isPromise(x: any): x is Promise<any> {
  return x && 'then' in x && typeof x.then === 'function';
}
